import { ComponentChildren, FunctionComponent, VNode } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { VComponent, classes } from '@adobe/elsie/lib';
import '@adobe/elsie/components/ActionButton/ActionButton.css';

export interface ActionButtonProps
  extends Omit<HTMLAttributes<HTMLButtonElement>, 'icon'> {
  children?: ComponentChildren;
  icon?: VNode<HTMLAttributes<SVGSVGElement>>;
  active?: boolean;
  disabled?: boolean;
}

export const ActionButton: FunctionComponent<ActionButtonProps> = ({
  icon,
  className,
  children,
  active = false,
  disabled = false,
  ...props
}) => {
  return (
    <button
      role="button"
      disabled={disabled}
      {...props}
      className={classes([
        'dropin-action-button',
        ['dropin-action-button--active', active],
        ['dropin-action-button--disabled', disabled],
        className,
      ])}
    >
      {icon && (
        <VComponent
          node={icon}
          className={classes(['dropin-action-button-icon'])}
        />
      )}
      {children &&
        (typeof children === 'string' ? <span>{children}</span> : children)}
    </button>
  );
};
